body {
    font-family: Arial, sans-serif;
    font-size: 16px;
  }
  
  body {
    background-color: #f0f0f0;
  }
  
  .container {
    max-width: 960px;
    margin: 0 auto;
  }
  
  h1 {
    font-size: 32px;
    font-weight: bold;
    margin-top: 40px;
  }
  
  p {
    line-height: 1.5;
    margin-bottom: 20px;
  }
  
  a {
    color: #0070c0;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    background-color: #0070c0;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #005ca9;
  }
  
  form {
    margin-top: 40px;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  input[type="text"], input[type="email"], textarea {
    display: block;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-bottom: 20px;
    box-sizing: border-box;
  }
  
  textarea {
    height: 150px;
  }
  
  input[type="submit"] {
    background-color: #0070c0;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  input[type="submit"]:hover {
    background-color: #005ca9;
  }

  .django{
    
    background-color: #00783a;
    display: inline-block;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
  }

  .django:hover{
    background-color: #015d2d;
  }

  table {
    border-collapse: collapse;
    width: 100%;
  }

  th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }

  th {
    background-color: #f2f2f2;
    color: #444;
    font-weight: bold;
  }

  tr:hover {
    background-color: #f5f5f5;
  }